@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom base styles */
@layer base {
  body {
    @apply bg-gray-900 text-white font-sans;
  }
  
  /* Smooth scrolling */
  html {
    scroll-behavior: smooth;
  }
}

/* Custom component styles */
@layer components {
  /* Button styles */
  .btn {
    @apply px-4 py-2 rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2;
  }
  
  .btn-primary {
    @apply bg-blue-600 hover:bg-blue-700 text-white focus:ring-blue-500;
  }
  
  .btn-secondary {
    @apply bg-gray-700 hover:bg-gray-600 text-white focus:ring-gray-500;
  }
  
  .btn-success {
    @apply bg-green-600 hover:bg-green-700 text-white focus:ring-green-500;
  }
  
  .btn-danger {
    @apply bg-red-600 hover:bg-red-700 text-white focus:ring-red-500;
  }
  
  /* Card styles */
  .card {
    @apply bg-gray-800 rounded-lg shadow-golf border border-gray-700;
  }
  
  .card-header {
    @apply px-6 py-4 border-b border-gray-700;
  }
  
  .card-body {
    @apply px-6 py-4;
  }
  
  /* Form styles */
  .form-input {
    @apply w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent;
  }
  
  .form-label {
    @apply block text-sm font-medium text-gray-300 mb-2;
  }
  
  /* Navigation styles */
  .nav-link {
    @apply flex items-center px-4 py-2 text-gray-300 rounded-lg hover:bg-gray-700 hover:text-white transition-colors;
  }
  
  .nav-link.active {
    @apply bg-blue-600 text-white;
  }
  
  /* Table styles */
  .table {
    @apply w-full text-sm text-left;
  }
  
  .table th {
    @apply px-4 py-3 text-gray-400 font-medium border-b border-gray-700;
  }
  
  .table td {
    @apply px-4 py-3 border-b border-gray-700;
  }
  
  .table tbody tr:hover {
    @apply bg-gray-700/50;
  }
}

/* Custom utility styles */
@layer utilities {
  /* Golf-specific utilities */
  .text-golf {
    @apply text-green-400;
  }
  
  .bg-grass {
    background: linear-gradient(135deg, #2d5016 0%, #3a5f1e 100%);
  }
  
  /* Animation utilities */
  .animate-fade-in {
    animation: fadeIn 0.5s ease-in-out;
  }
  
  .animate-slide-up {
    animation: slideUp 0.3s ease-out;
  }
}

/* Custom animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { 
    transform: translateY(10px); 
    opacity: 0; 
  }
  to { 
    transform: translateY(0); 
    opacity: 1; 
  }
}

